"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { cn } from "@/lib/utils";

type PlayerColor = "red" | "blue" | "green" | "yellow";
type TokenPosition = number; // 0-51 for board positions, -1 for home, 52+ for finish area

interface Player {
  color: PlayerColor;
  tokens: TokenPosition[];
  isActive: boolean;
}

interface GameState {
  players: Player[];
  currentPlayerIndex: number;
  diceValue: number | null;
  canRollDice: boolean;
  winner: PlayerColor | null;
  error: string;
}

const PLAYER_COLORS: Record<PlayerColor, string> = {
  red: "bg-red-500",
  blue: "bg-blue-500",
  green: "bg-green-500",
  yellow: "bg-yellow-500"
};

const PLAYER_LIGHT_COLORS: Record<PlayerColor, string> = {
  red: "bg-red-100",
  blue: "bg-blue-100",
  green: "bg-green-100",
  yellow: "bg-yellow-100"
};

const createInitialPlayers = (): Player[] => {
  const colors: PlayerColor[] = ["red", "blue", "green", "yellow"];
  return colors.map(color => ({
    color,
    tokens: [-1, -1, -1, -1], // All tokens start at home (-1)
    isActive: true
  }));
};

export default function LudoGame() {
  const [gameState, setGameState] = useState<GameState>({
    players: createInitialPlayers(),
    currentPlayerIndex: 0,
    diceValue: null,
    canRollDice: true,
    winner: null,
    error: ""
  });

  const rollDice = () => {
    try {
      if (!gameState.canRollDice || gameState.winner) {
        setGameState(prev => ({ ...prev, error: "Cannot roll dice right now!" }));
        return;
      }

      const diceValue = Math.floor(Math.random() * 6) + 1;
      
      setGameState(prev => ({
        ...prev,
        diceValue,
        canRollDice: false,
        error: ""
      }));

      // Check if player can move any token
      setTimeout(() => {
        const currentPlayer = gameState.players[gameState.currentPlayerIndex];
        const canMove = canPlayerMove(currentPlayer, diceValue);
        
        if (!canMove) {
          // No valid moves, pass turn
          nextTurn();
        }
      }, 1000);

    } catch (err) {
      setGameState(prev => ({ ...prev, error: "Error rolling dice!" }));
      console.error("Dice roll error:", err);
    }
  };

  const canPlayerMove = (player: Player, diceValue: number): boolean => {
    // Check if any token can be moved
    return player.tokens.some(tokenPos => {
      if (tokenPos === -1) {
        // Token at home can only move with 6
        return diceValue === 6;
      } else if (tokenPos >= 0 && tokenPos < 52) {
        // Token on board can move if it doesn't exceed finish
        return tokenPos + diceValue <= 56; // 52 board + 4 finish positions
      }
      return false;
    });
  };

  const moveToken = (tokenIndex: number) => {
    try {
      if (!gameState.diceValue || gameState.canRollDice || gameState.winner) {
        setGameState(prev => ({ ...prev, error: "Roll dice first!" }));
        return;
      }

      const currentPlayer = gameState.players[gameState.currentPlayerIndex];
      const currentPosition = currentPlayer.tokens[tokenIndex];

      // Validate move
      if (currentPosition === -1 && gameState.diceValue !== 6) {
        setGameState(prev => ({ ...prev, error: "Need 6 to move token from home!" }));
        return;
      }

      if (currentPosition >= 52) {
        setGameState(prev => ({ ...prev, error: "Token already in finish area!" }));
        return;
      }

      // Calculate new position
      let newPosition: number;
      if (currentPosition === -1) {
        // Moving from home
        newPosition = getStartPosition(currentPlayer.color);
      } else {
        // Moving on board
        newPosition = currentPosition + gameState.diceValue;
        if (newPosition > 56) {
          setGameState(prev => ({ ...prev, error: "Move would exceed finish area!" }));
          return;
        }
      }

      // Update game state
      setGameState(prev => {
        const newPlayers = [...prev.players];
        newPlayers[prev.currentPlayerIndex].tokens[tokenIndex] = newPosition;

        // Check for winner
        const winner = checkWinner(newPlayers[prev.currentPlayerIndex]);

        return {
          ...prev,
          players: newPlayers,
          winner,
          error: ""
        };
      });

      // Move to next turn (unless rolled 6 or won)
      if (gameState.diceValue !== 6 && !gameState.winner) {
        setTimeout(nextTurn, 500);
      } else {
        setGameState(prev => ({ ...prev, canRollDice: true, diceValue: null }));
      }

    } catch (err) {
      setGameState(prev => ({ ...prev, error: "Error moving token!" }));
      console.error("Move token error:", err);
    }
  };

  const getStartPosition = (color: PlayerColor): number => {
    const startPositions: Record<PlayerColor, number> = {
      red: 0,
      blue: 13,
      green: 26,
      yellow: 39
    };
    return startPositions[color];
  };

  const checkWinner = (player: Player): PlayerColor | null => {
    // Player wins when all tokens reach finish area (position 52+)
    const allTokensFinished = player.tokens.every(pos => pos >= 52);
    return allTokensFinished ? player.color : null;
  };

  const nextTurn = () => {
    setGameState(prev => ({
      ...prev,
      currentPlayerIndex: (prev.currentPlayerIndex + 1) % prev.players.length,
      canRollDice: true,
      diceValue: null
    }));
  };

  const resetGame = () => {
    try {
      setGameState({
        players: createInitialPlayers(),
        currentPlayerIndex: 0,
        diceValue: null,
        canRollDice: true,
        winner: null,
        error: ""
      });
    } catch (err) {
      setGameState(prev => ({ ...prev, error: "Error resetting game!" }));
      console.error("Reset game error:", err);
    }
  };

  const currentPlayer = gameState.players[gameState.currentPlayerIndex];

  return (
    <div className="max-w-4xl mx-auto p-4">
      <Card>
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">Ludo</CardTitle>
          <p className="text-muted-foreground">
            Roll the dice and race your tokens home! Get all 4 tokens to the finish area to win.
          </p>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Game Status */}
          <div className="text-center">
            {gameState.winner ? (
              <p className="text-lg font-semibold text-foreground">
                ðŸŽ‰ {gameState.winner.toUpperCase()} Player Wins! ðŸŽ‰
              </p>
            ) : (
              <p className="text-lg font-semibold text-foreground">
                Current Player: <span className={cn("px-2 py-1 rounded text-white", PLAYER_COLORS[currentPlayer.color])}>
                  {currentPlayer.color.toUpperCase()}
                </span>
              </p>
            )}
            {gameState.error && (
              <p className="text-destructive text-sm mt-2 bg-destructive/10 p-2 rounded">
                {gameState.error}
              </p>
            )}
          </div>

          {/* Dice Section */}
          <div className="text-center space-y-4">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-16 h-16 border-2 border-border rounded-lg flex items-center justify-center bg-white shadow-lg">
                <span className="text-2xl font-bold">
                  {gameState.diceValue || "?"}
                </span>
              </div>
              <Button 
                onClick={rollDice} 
                disabled={!gameState.canRollDice || !!gameState.winner}
                className="px-6 py-2"
              >
                Roll Dice
              </Button>
            </div>
          </div>

          {/* Simplified Board Representation */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {gameState.players.map((player, playerIndex) => (
              <Card key={player.color} className={cn("p-4", PLAYER_LIGHT_COLORS[player.color])}>
                <div className="text-center mb-3">
                  <h3 className={cn("font-bold text-white px-2 py-1 rounded", PLAYER_COLORS[player.color])}>
                    {player.color.toUpperCase()}
                  </h3>
                </div>
                
                {/* Player's Tokens */}
                <div className="grid grid-cols-2 gap-2">
                  {player.tokens.map((position, tokenIndex) => (
                    <button
                      key={tokenIndex}
                      onClick={() => moveToken(tokenIndex)}
                      disabled={
                        gameState.currentPlayerIndex !== playerIndex || 
                        gameState.canRollDice || 
                        !gameState.diceValue ||
                        !!gameState.winner
                      }
                      className={cn(
                        "w-12 h-12 rounded-full border-2 border-gray-300 flex items-center justify-center",
                        "transition-all duration-200 hover:scale-110 disabled:cursor-not-allowed",
                        PLAYER_COLORS[player.color],
                        position === -1 ? "opacity-50" : "opacity-100 shadow-lg",
                        position >= 52 ? "ring-4 ring-yellow-400" : ""
                      )}
                    >
                      <span className="text-white font-bold text-xs">
                        {position === -1 ? "H" : position >= 52 ? "F" : position}
                      </span>
                    </button>
                  ))}
                </div>
                
                <div className="text-xs text-center mt-2 text-muted-foreground">
                  Tokens: {player.tokens.filter(pos => pos >= 52).length}/4 finished
                </div>
              </Card>
            ))}
          </div>

          {/* Game Controls */}
          <div className="flex justify-center space-x-4">
            <Button onClick={resetGame} variant="outline">
              New Game
            </Button>
          </div>

          {/* Game Rules */}
          <div className="text-sm text-muted-foreground bg-muted/30 p-4 rounded-lg">
            <h4 className="font-semibold mb-2">How to Play:</h4>
            <ul className="space-y-1">
              <li>â€¢ Roll the dice to move your tokens</li>
              <li>â€¢ Need a 6 to move tokens from home (H)</li>
              <li>â€¢ Rolling a 6 gives you another turn</li>
              <li>â€¢ Get all 4 tokens to the finish area (F) to win</li>
              <li>â€¢ Click on a token to move it after rolling the dice</li>
            </ul>
            <p className="mt-2 text-xs">
              <strong>Note:</strong> This is a simplified Ludo implementation. 
              Features like capturing opponent tokens and safe squares are not included.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
